import React from 'react';
import styled from 'styled-components';
import {  } from 'prop-types';

const url = 'https://api.github.com/users/gaearon/repos';
const MAX_ITEM = 10;
const NON_EXTENDED_ITEM = 5;

const StyledListItem = styled.li`
    list-style: none;
    display: flex;
    justify-content: space-between;
`;

const StyledList = styled.ul`
    padding: 0;
    margin: 0;
`;

const Results = (props) => {
    const[list, useList] = React.useState([]);
    const[isExtended, setIsExtended] = React.useState(false);

    React.useEffect(() => {
        const req = async () => {
            const response = await (await fetch(url)).json();
            const out = response.slice(0, MAX_ITEM);
            useList(out);
        }
        req()
    }, [])

    const listToRender = isExtended ? list : list.slice(0, NON_EXTENDED_ITEM);

    return (
        <div>
            <StyledList>
                {listToRender.map((item, index) => {
                    return (
                        <StyledListItem key={item.id}>
                            <span>{index + 1}.</span>
                            <span>{item.full_name}</span>
                            <span>{item.size}</span>
                        </StyledListItem>
                    )
                })}
            </StyledList>
            {isExtended ? null : <button onClick={() => setIsExtended(true)}>Zobrazit další</button>}
        </div>
    );
};

Results.propTypes = {
    
}

Results.defaultProps = {
    
}

export default Results;